		padding: get-rem(48) get-rem(115);
	}
}

.list-slider {

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		gap: get-rem(40);
		margin: 0 0 get-rem(48);

		@media(min-width: 768px) {
			margin: 0 0 get-rem(71);
		}

		@media(min-width: 992px) {
			margin: 0 0 get-vw(98);
		}

		@media(min-width: 1921px) {
			margin: 0 0 get-rem(98);
		}

		.title span {
			margin: 0 0 0 get-rem(13);

			@media(min-width: 768px) {
				margin: 0 0 0 get-rem(19);
			}

			@media(min-width: 992px) {
				margin: 0 0 0 get-vw(34);
			}

			@media(min-width: 1921px) {
				margin: 0 0 0 get-rem(34);
			}
		}

		.swiper-nav {
			padding: 0 get-rem(5) 0 0;

			@media(min-width: 992px) {
				padding: 0;
				transform: translateY(#{get-rem(-19)});
			}
		}
	}

	&__slider {
		width: calc(100% + 8.5vw);
		overflow: visible;
	}

	.title--section-semi {
		@media(max-width: 479px) {
		    font-size: 8vw;

			span {
				font-size: 5vw;
			}
		}
	}
}

.swiper-nav {
	display: flex;
	align-items: center;
	gap: get-rem(8);

	@media(min-width: 768px) {
		gap: get-rem(16);
	}
}

.slide-item {
	display: block;
	width: 100%;
	border-radius: get-rem(24);

	@media(min-width: 992px) {
		border-radius: get-rem(40);
	}

	&::after {
		content: '';
		border-radius: inherit;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		background-color: var(--color-purple);
		transform: rotate(4deg) translateY(#{get-rem(-6)});
		transform-origin: bottom right;
		transition: transform 0.15s ease-in;
	}

	&__img {
		display: block;
		width: 100%;
		position: relative;
		z-index: 2;
		height: 0;
		padding-bottom: 73.31%;
		border-radius: inherit;
		overflow: hidden;

		img {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
			transform-origin: center;
			transition: transform 0.15s ease-in;
		}
	}

	.desktop-device & {
	    &:hover {
		    &::after {
			    transform: rotate(0) translateY(0);
		    }

		    img {
			    transform: scale(1.03);
		    }
	    }
	}
}

.swiper-btn {
	display: flex;
	justify-content: center;
	align-items: center;
	width: get-rem(32);
	height: get-rem(32);
	border-radius: 100%;
	background-color: var(--color-blue);
	transition: background-color 0.15s ease-in;

	@media(min-width: 768px) {
		width: get-rem(56);
		height: get-rem(56);
	}

	.swiper-lock {
		opacity: 0.2;
		pointer-events: none;
	}

	&.swiper-button-lock {
		display: none;
	}

	svg {
		width: 48%;
		height: 48%;
	}

	.desktop-device & {
		&:hover {
			background-color: var(--color-purple);
		}
	}
}
